import axios from "axios";
import React, { useState, useEffect } from "react";
import { Redirect } from "react-router";
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';


const useStyles = makeStyles((theme) => ({
    paper: {
      marginTop: theme.spacing(8),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
    container: {
      backgroundColor: theme.palette.background.paper,
      paddingBottom: 20,
  }
  }));

const NewComment = ({postID}) => {
    const [comment, setComment] = useState('');
    const [owner, setOwner] = useState('');
    const [redirect, setRedirect] = useState(false)
    const blog_post = postID
    const classes = useStyles();

    useEffect(() => {
        (
            async () => {
                const response = await fetch('http://worker.std-1378.ist.mospolytech.ru/api/user', {
                    headers: { 'Content-Type': 'application/json' },
                    credentials: 'include'
                });
                const content = await response.json();
                setOwner(content.id);
            }
        )();
    })

    const submit = async (e) => {
        e.preventDefault()
        await fetch('http://worker.std-1378.ist.mospolytech.ru/api/comments/', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                owner,
                comment,
                blog_post
            })
        })

        setTimeout(() => setRedirect(true), 100)
    }
    if (redirect)
        return <Redirect to={{pathname:`/posts/${blog_post}`}} />


    return (
            <Container component="main" maxWidth="xs" className={classes.container}>
            <CssBaseline />
                <form onSubmit={submit} className={classes.form}>
                    <fieldset>
                        <legend>
                        <Typography component="h1" variant="h5">
                        Ваш комментарий
                        </Typography>
                        </legend>
                        <TextField
                        id="outlined-multiline-static"
                        label="Multiline"
                        multiline
                        rows={4}
                        placeholder="Комментарий"
                        variant="outlined"
                        onChange={e => setComment(e.target.value)}
                        />
                        <Button type="submit" variant="contained" color="primary">
                        Добавить
                        </Button>
                    </fieldset>
                </form>
            </Container>
    );
};

export default NewComment;
